import { login, thirdPartyLogin, BASE_URL } from '@/api'
import thirdLogin from '@/components/third-login.vue'
import inputCell from '@/components/input-cell.vue'
export default {
	data() {
		return {
			phone: '',
			pwd: '',
			animationData: {}
		};
	},
	components: {
		thirdLogin,
		inputCell
	},
	watch: {

	},
	onLoad () {

	},
	onShow () {










	},
	methods: {
		// 三方登录
		async thirdLoginClick (e) {
			if(e === 1) {   // 微信
				uni.login({
					provider: 'weixin',
					success: function (loginRes) {
						console.log(JSON.stringify(loginRes));
						uni.showLoading({
							title: '获取权限中...'
						})
						// 获取用户信息
						uni.getUserInfo({
							provider: 'weixin',
							success: function (infoRes) {
								thirdPartyLogin({
									openid: infoRes.userInfo.openId,
									platform: "wechat"
								}).then(res => {
									console.log(JSON.stringify(res))
									uni.hideLoading()
									if(res.recode == 10000) {
										uni.setStorage({
											key: 'token',
											data: res.data.token
										})
										uni.reLaunch({
											url: '/pages/home/index/index',
										})
									}else if (res.recode === 10017) {
										uni.navigateTo({
											url: '/pages/login/verifyPhone/verifyPhone?third_id=' + res.data.third_id
										})
									}else {
										uni.showToast({
											title: res.remsg,
											icon: 'none'
										})
									}
								})
							}
						});
					}
				});
			}
			if(e === 2) {   // 支付宝
				uni.showToast({
					title: '暂未开放，敬请期待',
					icon: 'none'
				})
			}
		},
		// 登录
		async userLogin () {
			let result = await login({
				mobile: this.phone,
				password: this.pwd
			})
			if(result.recode == 10000) {
				uni.setStorage({
					key: 'token',
					data: result.data.token
				})
				uni.reLaunch({
					url: '/pages/home/index/index',
				})
			}else {
				uni.showToast({
					title: result.remsg,
					icon: 'none'
				})
			}
		},	
	}
}
